.list-main {
  width: 100%;
  max-width: $max-width; /* formerly 800px */
  text-align: center;
  padding: 20px;
  margin: 20px auto;

  @media #{$media-size-tablet} {
      max-width: 800px; /* formerly 600px */
      margin: 0 auto;
  }

  .list-container {

      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;

      .list-item {
          border: 3px solid;
          border-radius: 0.5rem;
          background-color: $dark-background-secondary;
          text-decoration: none;
          text-align: center;
          margin: 1.5rem;
          padding: 1.5rem;
          max-width: 200px;
          position: relative;

          @media #{$media-size-tablet} {
              margin: 1.0rem;
              padding: 1.0rem;
          }

          .list-item-label {
              margin: 0.5rem auto;
              font-weight: bold;
              letter-spacing: -0.1rem;
              font-size: 1.25rem;
          }

          .list-item-overlay {
              position: absolute;
              z-index: 10;
              width: 100%;
              height: 100%;
              top: 0;
              left: 0;
              background-color: rgba(0, 0, 0, 0.35);
              display: flex;
              justify-content: center;
              align-items: center;
              span {
                  font-weight: bolder;
                  font-size: 2.5rem;
                  text-transform: uppercase;
                  transform: rotate(-45deg);
                  filter: brightness(1.5);
              }
          }
      }
  }

}

.pagination {
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: center;
    
    li {
        padding: 0.5rem;
        margin: 0 0.25rem;
        text-decoration: none;
        &.active {
            border-top: 2px solid;
            border-bottom: 2px solid;
            border-radius: 0.25rem;
        }
        a {
            text-decoration: none;
        }
    }
}
